.side-navigation {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

.side-navigation-transform {
    width: 100%;
    height: 100%;
    -webkit-transition: 0.2s ease-in-out;
    -o-transition: 0.2s ease-in-out;
    transition: 0.2s ease-in-out;
}

.side-navigation-navbar {
    overflow: hidden;
    height: 100%;
    z-index: 400;
}

.side-navigation-hamburger {
    right: 15px;
    position: absolute;
    z-index: 800;
    -webkit-transform: translateY(30%);
        -ms-transform: translateY(30%);
            transform: translateY(30%);
}

.side-navigation-side-panel {
    z-index: 700;
}

.side-navigation-window {
    width: 100%;
    height: 100%;
    -webkit-overflow-scrolling: touch;
    overflow-y: auto;
    overflow-x: hidden;
    z-index: 300;
}

.side-navigation-mask {
    position: fixed;
    width: 100%;
    height: 100%;
    opacity: 0.8;
}

.side-navigation-mask-appear {
    -webkit-animation: mask-appear 0.2s forwards;
            animation: mask-appear 0.2s forwards;
    z-index: 400;
}

.side-navigation-mask-hide {
    background-color: white;
    -webkit-animation: mask-disappear 0.2s forwards;
            animation: mask-disappear 0.2s forwards;
    z-index: -100;
}

@-webkit-keyframes mask-appear {
    0% {
        background-color: #444444;
    }

    100% {
        background-color: black;
    }
}

@keyframes mask-appear {
    0% {
        background-color: #444444;
    }

    100% {
        background-color: black;
    }
}

@-webkit-keyframes mask-disappear {
    0% {
        background-color: black;
    }

    100% {
        background-color: #444444;
    }
}

@keyframes mask-disappear {
    0% {
        background-color: black;
    }

    100% {
        background-color: #444444;
    }
}



